// __tests__/screens/Dashboard/components/HeroContinueSection.test.js
import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import HeroContinueSection from '../../../../src/screens/Dashboard/components/HeroContinueSection';
import { ThemeContext } from '../../../../src/contexts/ThemeContext';

// Mock du composant Card
jest.mock('../../../../src/components/ui/Card', () => {
  const { View } = require('react-native');
  return ({ children, style, ...props }) => (
    <View style={style} {...props} testID="card">
      {children}
    </View>
  );
});

describe('HeroContinueSection', () => {
  const mockThemeContext = {
    colors: {
      surface: '#FFFFFF',
      text: '#1F2937',
      textSecondary: '#6B7280'
    }
  };

  const mockOnPress = jest.fn();

  const mockLastActivity = {
    title: 'Vocabulaire Animaux',
    level: '2',
    type: 'vocabulary',
    metadata: {
      word: 4,
      totalWords: 15,
      categoryIndex: 1
    }
  };

  const renderWithTheme = (component, themeValue = mockThemeContext) => {
    return render(
      <ThemeContext.Provider value={themeValue}>
        {component}
      </ThemeContext.Provider>
    );
  };

  beforeEach(() => {
    jest.clearAllMocks();
  });

  describe('État de chargement', () => {
    it('devrait afficher l\'indicateur de chargement', () => {
      const { getByText, getByTestId } = renderWithTheme(
        <HeroContinueSection isLoading={true} onPress={mockOnPress} />
      );

      expect(getByText('Chargement...')).toBeTruthy();
      expect(getByTestId('card')).toBeTruthy();
    });

    it('devrait utiliser la couleur d\'accent pour l\'indicateur', () => {
      const customColor = '#FF5722';
      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          isLoading={true} 
          accentColor={customColor}
          onPress={mockOnPress} 
        />
      );

      expect(getByText('Chargement...')).toBeTruthy();
    });
  });

  describe('État vide (première utilisation)', () => {
    it('devrait afficher le message de démarrage', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection lastActivity={null} onPress={mockOnPress} />
      );

      expect(getByText('🚀')).toBeTruthy();
      expect(getByText('Commencer l\'apprentissage')).toBeTruthy();
      expect(getByText('Choisissez votre premier exercice')).toBeTruthy();
      expect(getByText('Commencer >')).toBeTruthy();
    });

    it('devrait appeler onPress avec "levelSelection" au clic', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection lastActivity={null} onPress={mockOnPress} />
      );

      const button = getByText('Commencer >');
      fireEvent.press(button);

      expect(mockOnPress).toHaveBeenCalledWith('levelSelection');
    });
  });

  describe('État avec activité existante', () => {
    it('devrait afficher les informations de l\'activité', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={mockLastActivity} 
          onPress={mockOnPress} 
        />
      );

      expect(getByText('📚')).toBeTruthy();
      expect(getByText('Reprendre')).toBeTruthy();
      expect(getByText('Vocabulaire Animaux')).toBeTruthy();
      expect(getByText('Niv 2 • Catégorie 2 • Mot 5/15')).toBeTruthy();
      expect(getByText('Continuer ▶️')).toBeTruthy();
    });

    it('devrait calculer et afficher le pourcentage correct', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={mockLastActivity} 
          onPress={mockOnPress} 
        />
      );

      // (4+1)/15 * 100 = 33%
      expect(getByText('33%')).toBeTruthy();
    });

    it('devrait appeler onPress avec l\'activité au clic sur continuer', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={mockLastActivity} 
          onPress={mockOnPress} 
        />
      );

      const button = getByText('Continuer ▶️');
      fireEvent.press(button);

      expect(mockOnPress).toHaveBeenCalledWith(mockLastActivity);
    });

    it('devrait gérer une activité sans métadonnées', () => {
      const activityWithoutMetadata = {
        title: 'Grammaire Simple',
        level: '1',
        type: 'grammar'
      };

      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={activityWithoutMetadata} 
          onPress={mockOnPress} 
        />
      );

      expect(getByText('Grammaire Simple')).toBeTruthy();
      expect(getByText('Niv 1 • Mot 1/15')).toBeTruthy(); // Valeurs par défaut
      expect(getByText('7%')).toBeTruthy(); // 1/15 * 100 = 7%
    });

    it('devrait gérer une activité sans categoryIndex', () => {
      const activityWithoutCategory = {
        ...mockLastActivity,
        metadata: {
          word: 9,
          totalWords: 20
        }
      };

      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={activityWithoutCategory} 
          onPress={mockOnPress} 
        />
      );

      expect(getByText('Niv 2 • Mot 10/20')).toBeTruthy(); // Pas de catégorie
      expect(getByText('50%')).toBeTruthy(); // 10/20 * 100 = 50%
    });

    it('devrait limiter le pourcentage à 100%', () => {
      const completedActivity = {
        ...mockLastActivity,
        metadata: {
          word: 20,
          totalWords: 15 // Plus de mots que le total
        }
      };

      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={completedActivity} 
          onPress={mockOnPress} 
        />
      );

      expect(getByText('100%')).toBeTruthy(); // Limité à 100%
    });
  });

  describe('Couleurs et thème', () => {
    it('devrait utiliser la couleur d\'accent personnalisée', () => {
      const customColor = '#8B5CF6';
      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={mockLastActivity}
          accentColor={customColor}
          onPress={mockOnPress} 
        />
      );

      expect(getByText('33%')).toBeTruthy();
      expect(getByText('Continuer ▶️')).toBeTruthy();
    });

    it('devrait utiliser la couleur par défaut si aucune couleur n\'est spécifiée', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={mockLastActivity}
          onPress={mockOnPress} 
        />
      );

      expect(getByText('Continuer ▶️')).toBeTruthy();
    });

    it('devrait fonctionner sans contexte de thème', () => {
      const { getByText } = render(
        <HeroContinueSection 
          lastActivity={mockLastActivity}
          onPress={mockOnPress} 
        />
      );

      expect(getByText('Vocabulaire Animaux')).toBeTruthy();
      expect(getByText('Continuer ▶️')).toBeTruthy();
    });
  });

  describe('Gestion des callbacks', () => {
    it('ne devrait pas planter si onPress n\'est pas fourni', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection lastActivity={null} />
      );

      const button = getByText('Commencer >');
      expect(() => fireEvent.press(button)).not.toThrow();
    });

    it('ne devrait pas planter si onPress est undefined pour une activité', () => {
      const { getByText } = renderWithTheme(
        <HeroContinueSection lastActivity={mockLastActivity} />
      );

      const button = getByText('Continuer ▶️');
      expect(() => fireEvent.press(button)).not.toThrow();
    });
  });

  describe('Cas limites', () => {
    it('devrait gérer un niveau manquant', () => {
      const activityWithoutLevel = {
        title: 'Test Activity',
        type: 'vocabulary'
      };

      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={activityWithoutLevel}
          onPress={mockOnPress} 
        />
      );

      expect(getByText('Niv 1 • Mot 1/15')).toBeTruthy(); // Niveau par défaut
    });

    it('devrait gérer des métadonnées partielles', () => {
      const activityWithPartialMetadata = {
        title: 'Test Activity',
        level: '3',
        metadata: {
          totalWords: 25
          // word manquant
        }
      };

      const { getByText } = renderWithTheme(
        <HeroContinueSection 
          lastActivity={activityWithPartialMetadata}
          onPress={mockOnPress} 
        />
      );

      expect(getByText('Niv 3 • Mot 1/25')).toBeTruthy(); // word par défaut à 0
      expect(getByText('4%')).toBeTruthy(); // 1/25 * 100 = 4%
    });
  });
});